/* css reset */
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0
}

*::before,
*::after {
    box-sizing: border-box;
}

ul,
ol {
    list-style: none;
}

a {
    color: inherit;
    text-decoration: none;
}

img {
    max-width: 44px;
    max-height: 44px;
}

svg {
    max-width: 100%;
    max-height: 100%;
}

/* style */
body {
    background-color: #fff;
    /* background: #FFF url("https://cn.bing.com/th?id=OHR.PurpleFlowers_ZH-CN7975901617_1920x1080.jpg&amp;rf=LaDigue_1920x1080.jpg&amp;pid=hp") repeat-x 0 */
    height: 100vh;
}

.header {
    margin: 20px;
}

.cat {
    text-align: center;
    min-height: 100px;
    width: 300px;
    margin-left: auto;
    margin-right: auto;
    background: #FFF url("../images/cat.png") no-repeat 0
}

.search {
    height: 44px;
    border: 1px solid #dfe1e5;
    border-radius: 20px;
    background-color: #fff;
}

.search:hover {
    background-color: #fff;
    box-shadow: 0 1px 6px rgb(32 33 36 / 28%);
    border-color: rgba(223, 225, 229, 0);
}

.searchForm {
    display: flex;
    justify-content: space-between;
}

.searchForm>input {
    width: 90%;
    height: 40px;
    border: none;
    border-radius: 4px;
    margin-left: 22px;
}

.searchForm>input:focus {
    outline: none;
}

.searchForm>button {
    padding: 10px;
    border: none;
    height: 40px;
    border-radius: 20px;
    background-color: #fff;
}

.main {
    max-width: 900px;
    margin-left: auto;
    margin-right: auto;
}

.siteList {
    margin: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.site {
    width: 160px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: #eee;
    /* border: 1px solid #ddd; */
    border-radius: 4px;
    padding: 20px 0;
    position: relative;
    margin-bottom: 10px;
    cursor: pointer;
}

.site>.logo {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
    margin-bottom: 15px;
    text-transform: uppercase;
    border: 1px solid #fff;
    border-radius: 50%;
    background-color: #fff;
}

.site>.link {
    font-size: 14px;
    color: #707070;
}

.site>.close {
    height: 24px;
    width: 24px;
    position: absolute;
    top: 5px;
    right: 5px;
    cursor: default;
}


@media(min-width:500px) {
    .header {
        margin: 60px 0;
    }

    .search,
    .cat {
        max-width: 500px;
        margin-left: auto;
        margin-right: auto;
    }

    .cat {
        width: auto;
        background: #FFF url("../images/cat_long.png") no-repeat 0
    }

    .siteList {
        justify-content: flex-start;
        margin-left: 0;
        margin-right: -25px;
    }

    .site {
        margin-right: 25px;
    }

    .site>.close {
        display: none;
    }

    .site:hover>.close {
        display: block;
    }
}